.services-box{
    padding-top: var(--top-bage-size);
    font-family: var(--site-font);
}
.services-types-box{
    margin-top: 20px;
    display: flex;
    background-color: white;
    height: 400px;

}
.services-types-box .image-services{
    object-fit: cover;
    width: 65%;
}
.services-types-box .description-part{
    background-image: url('../../../assets/images/services-swiper/white-pattern.jpg');
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    gap: 30px;
    padding: 50px;
    width: 45%;
    position: relative;
}
.services-types-box .description-part::before{
    content: '';
    background-image: url('../../../assets/images/services-swiper/white-pattern.jpg');

     position: absolute;
    left: -90px;
    width: 100px;
    height: 100%;
    clip-path: polygon(0 0,100% 0,100% 100%,90% 100%);

}
.services-types-box .description-part .description-part-bg{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    background-image: url('../../../assets/images/services-swiper/white-pattern.jpg');
}
.services-types-box .description-part .description-tittle-h3{
    font-size: 25px;
}
.description-tittle-h3 strong{
    font-size: 30px;
}
.description-btn{
    font-weight: bold;
    text-decoration: none;
    text-transform: capitalize;
    color: black;
    transition: 0.5s ease;
    position: relative;
}
.description-btn::after{
    content: '';
    position: relative;
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(-45deg);
    left: 10px;
    transition: 0.5s ease;
}
.description-btn::before{
    content: '';
    width: 60px;
    height: 60px;
    border: solid var(--golden-color) 5px;
    position: absolute;
    border-top: none;
    border-right: none;
    top: -20px;
    left: -20px;
}
.description-btn:hover{
    color:  var(--golden-color);
}
.description-btn:hover::after{
    border-color: var(--golden-color);
    left: 15px;
}
.swiper-button-next, .swiper-button-prev {
    color: var(--main-color) !important;
}
.swiper-pagination-bullet-active {
    background: var(--main-color) !important;
}


@media (min-width: 768px) and (max-width: 1024px) {
    .services-types-box {
        height: 300px;
    }
    .services-types-box .description-part .description-tittle-h3 {
        font-size: 18px;
    }
    .services-types-box .description-part {
        padding: 35px;
    }
    .description-btn {
        font-size: 13px;
    }

}




/*mobiles*/
@media (min-width: 0px) and (max-width: 768px) {
    .services-types-box .image-services {
        display: none;
    }
    .services-types-box .description-part {
         width: auto;
    }
    .services-types-box .description-part .description-tittle-h3 {
        font-size: 20px;
        text-align: center;
    }
    .services-types-box .description-part::before{
        display: none;
    }
    .description-btn {
        font-size: 15px;
    }
    .swiper-button-prev,.swiper-button-next{
        display: none !important;
    }
}
