#menu-video-bg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
  }
.menu-box{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--banner-color);
    background-image: url('../../../assets/images/menu-video-bg.mp4');
    height: 110vh;
    overflow: hidden;
    position: relative;
    z-index: -1;
    transform: translateY(120%);
    transition: 0.5s ease;
    overflow: hidden;
}

.menu-box ul li{
    display: flex;
    justify-content: center;
    list-style: none;
    height: 100px;
    cursor: pointer;
}
.menu-box ul li a{
    color: white;
    text-decoration: none;
    font-family: var(--site-font);
    text-transform: capitalize;
    font-size: 50px;
    text-align: center;
    transition: 0.5s ease;

}
.menu-box ul li a span{
    display: inline-block;
    transform: scale(0);
    transition: 0.5s ease;

}
.menu-box ul li a:hover{
    font-size: 30px;
    transition: 0.5s ease;
    color: var(--light-color);
}
.menu-box ul li a:hover > span{
    display: block;
    transform: scale(1);
    font-size: 45px;
    color: var(--main-color);
}

.active{
    color: var(--main-color);
}





 /* tablets  */
 @media (min-width: 768px) and (max-width: 1024px) {
    .menu-box ul li a {
        font-size: 40px;
    }
    .menu-box ul li a:hover > span{
        font-size: 30px;
    }
   
    
}
/* mobiles */
@media (min-width: 320px) and (max-width: 767px) {
    .menu-box ul li a {
        font-size: 30px;
    }
    .menu-box ul li a:hover{
        font-size: 20px;
    }
    .menu-box ul li a:hover > span{
        font-size: 25px;
    }
 
  
}


