.digital-agency{
    padding-top: var(--top-bage-size);
    font-family: var(--site-font);
    background-image: url(../../assets//images/digital-agency-bg.jpg);
    background-size: cover;
}
.digital-agency .cases-container{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    gap: 60px;
}
.digital-agency .cases-container .case{
    width: 100%;
    position: relative;

}



.digital-agency .cases-container .case .case-bg{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.case-details{
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: space-around;
    top: -10%;
    left: 4%;
}
.case:nth-child(2) .case-details {
    flex-direction: column;
    flex-direction: row-reverse;
    gap: 25%
}
.case:nth-child(2) .border-case{
    border-left: none;
    border-right: 7px solid var(--golden-color);
    right: -0.5%;
    left: auto;
}

.digital-agency .cases-container .case .case-description{

    width: 50%;
}
.digital-agency .cases-container .case .case-description .case-logo{
    width: 15%;
    margin-bottom: 1%;
}
.digital-agency .cases-container .case .case-description .case-tittle-h3{
    color: white;
    margin-bottom: 1%;
    width: 65%;
    font-size: 20px;
}
.arrow-a{
    color: white;
}
.site-img{
    width: 40%;
    transition: 0.5S ease;
}


.case .border-case{
    width: 22%;
    height: 58%;
    position: absolute;
    border: 7px solid var(--golden-color);
    left: -0.5%;
    top: 42.4%;
    border-right: none;
    border-top: none;
    transition: 0.5S ease;
}

.case:hover .border-case{
    width: 40%;
    height: 30%;
    top: 70.4%;
}

.case:hover .site-img{
    width: 42%;
}

.case:nth-child(3) .case-logo,
.case:nth-child(2) .case-logo{
    width: 50% !important;
}



@media (min-width: 0px) and (max-width: 786px) {
    .digital-agency .cases-container {
    }
    .digital-agency .cases-container .case {
        height: 400px;
    }
    .case-details {
        left: 0;
        flex-direction: column-reverse;
        text-align: center;
        top: 0
    }
    .site-img {
        width: 45%;
    }
    .digital-agency .cases-container .case .case-description .case-tittle-h3 {
        margin-bottom: 1%;
        width: fit-content;
        font-size: 20px;
    }
    .case:nth-child(2) .case-details {
        flex-direction: column-reverse;
        gap: 25%;
    }
}

@media (min-width: 0px) and (max-width: 425px) {
    .case-details {
        top: 3%;
    }
    .site-img {
        width: 60%;
    }
    .digital-agency .cases-container .case .case-description .case-logo {
        width: 30%;
    }
    .digital-agency .cases-container .case .case-description .case-tittle-h3 {
        font-size: 18px;
    }
}

