@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;1,200&display=swap');
:root{
    --main-color : #04d0f1;
    --light-color : #04d1f196;
    --secondary-color :rgba(21,55,108,0.9);
    --banner-color : #3C4A64;
    --golden-color :#fcb900;
    --main-font: 'Roboto', sans-serif;
    --site-font: 'Nunito', sans-serif;
    --shadow-effect: 0px 0px 20px var(--main-color) inset;
    --padding-btn : 5px 12px 5px 12px;
    --font-size-title : 35px;
    --font-size-btn : 20px;
    --font-size-btn-mobile : 15px;
    --top-bage-size:3rem;

}
.box-btn{
    font-size: var(--font-size-btn);
    padding:var(--padding-btn);
    border: 1px solid white;
    background-color: #ffffff40;
    color: white;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    font-family:  var(--site-font);
    box-shadow: 0px 0px 0px var(--main-color) inset;
    transition: 0.5s ease;

}
.box-btn:hover{
    box-shadow: var(--shadow-effect);
}
.tittle-section-h2,.sub-tittle{
    font-family: var(--main-font) !important;
    color: white;
    text-align: center;
    text-transform: capitalize;
    font-size: var(--font-size-title);
    position: relative;
    z-index: 5;
}
.sub-tittle {
    font-size: 22px;
}

.container{
    padding-left: 3rem;
    padding-right: 3rem;
    max-width: 1450px;
    margin: 0 auto;
}

@media (min-width: 320px) and (max-width: 767px) {

    .box-btn{
        font-size: var(--font-size-btn-mobile);
    }
}
@media (min-width: 0px) and (max-width: 768px) {
    .container{
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }


}