.main-section{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: var(--banner-color);
    background-image: url(../../assets/images/main-banner.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    color: white;
    font-family: var(--site-font);
    text-transform: capitalize;
    text-align: center;
    position: relative;
    background-blend-mode: multiply;
    height: 100vh;
}
.main-section .panner-tittle-h1{
    font-size: 55px;
}
.main-section .panner-tittle-h2{
    font-size: 35px;
    margin-bottom: 2rem;
    margin-top: 1rem;
}
.main-section .ask-for-open-video{
    font-size: 20px;
    margin-bottom: 1rem;
}
.main-section .video-open-btn{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
    text-transform: uppercase;
    margin-bottom: 1rem;
    transition: 0.4s ease;

}
.main-section .video-open-btn .video-circle-player{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 100%;
    border: 2px solid white;
    margin: 8px;
    transition: 0.4s ease;
}

.main-section .video-open-btn .video-circle-player .triangle{
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-left: 16px solid white;
    border-bottom: 8px solid transparent;
    transition: 0.2s ease;

}

.main-section .video-open-btn:hover{
    color: var(--main-color);
}
.main-section .video-open-btn:hover .video-circle-player{
    border: 2px solid var(--main-color);
}
.main-section .video-open-btn:hover .triangle{
    border-left: 16px solid var(--light-color);
    transform: rotate(360deg)
}
/*video styling */
.video_ads_bg{
    position: absolute;
    z-index: 1;
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: none;
}
.video-close-btn{
    position: absolute;
    z-index: 2;
    bottom: 1.5rem;
    right: 1.5rem;
    font-size: 30px;
    opacity: 0.6;
    border: solid var(--light-color);
    background: none;
    color: white;
    padding: 20px;
    border-radius: 100%;
    cursor: pointer;
    transition: 0.3s ease;
    display: none;
}
.video-close-btn:hover{
    opacity: 1;

}








 /* tablets  */
@media (min-width: 768px) and (max-width: 1023.9px) {
    .main-section .panner-tittle-h1{
        font-size: 45px;
    }
    .main-section .panner-tittle-h2{
        font-size: 25px;;
    }
    .main-section .ask-for-open-video{
        font-size: 20px;
    }
    .main-section .video-open-btn{
        font-size: 15px;
    }

}
/* mobiles */
@media (min-width: 0px) and (max-width: 767.9px) {
    .main-section .panner-tittle-h1{
        font-size: 30px;
    }
    .main-section .panner-tittle-h2{
        font-size: 20px;;
    }
    .main-section .ask-for-open-video{
        font-size: 15px;
    }
    .main-section .video-open-btn{
        font-size: 15px;
    }

}

