.FAQs{
    padding: var(--top-bage-size);
    background-image: url('../../assets/images/FAQs-bg.jpg');
    font-family: var(--site-font);
    counter-reset: countElement;

}
.FAQs-box{
    max-width: 1450px;
    margin: var(--top-bage-size) auto;
    list-style: none;
    color: white;
}
.FAQs-box .FAQs-element{
    margin-bottom: 3%;
    position: relative;
    cursor: pointer;
}
.FAQs-box .FAQs-element::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: white;
    opacity: 0.6;
    margin: 1.5% 0;
}
.FAQs-box .FAQs-element h3{
    font-size: 30px;
    position: relative;
    padding-left: 80px;

}
.FAQs-box .FAQs-element h3::after{
    content: '+';
    color: white;
    position: absolute;
    right: 0;
    transition: 0.3s ease-out;
    margin-left: 30px;

}
.FAQs-box .FAQs-element h3.active::after{
    transform: rotate(315deg);
    color: var(--light-color);
}
.FAQs-box .FAQs-element h3::before{
    counter-increment: countElement;
    content: '0'counter(countElement);
    color: white;
    position: absolute;
    left: 0;
    opacity: 0.4;
}
.FAQs-box .FAQs-element h3.active::before{
    color: var(--light-color);
}

.FAQs-box .FAQs-element p{
    padding: 20px 90px;
    font-size: 20px;
    display:none;
    transition: 0.5s ease;
    opacity: 0;
}
@media (min-width: 425px) and (max-width: 768px) {
    .FAQs-box .FAQs-element h3 {
        font-size: 20px;
        padding-left: 34px;
    }
}

@media (min-width: 0px) and (max-width: 768px) {
    .FAQs-box .FAQs-element h3 {
        font-size: 18px;
        padding-left: 34px;
    }
    .FAQs-box .FAQs-element p {
        padding: 16px 22px;
        font-size: 15px
    }
    .FAQs {
        padding: var(--top-bage-size) 1.5rem;
    }
    .FAQs-box .FAQs-element {
        margin-bottom: 5%;
    }
}