.servicesAndBrojects{
    background-image: url('../../assets//images/services-bg.webp');
    background-size: cover;
}
.servicesAndBrojects .logo-bg{
    position: absolute;
    z-index: 0;
    text-transform: uppercase;
    font-family: var(--main-font);
    color: white;
    font-size: 100px;
    font-weight: bold;
    opacity: 0.1;
    transform: translateX(-50%);
    left: 50%;
    cursor: context-menu;
    white-space: nowrap;
}
.servicesAndBrojects .logo-bg span{
    color: var(--main-color)

}
@media (min-width: 425px) and (max-width: 786px){
    .servicesAndBrojects .logo-bg{
        font-size: 61px;
    }

}
@media (min-width: 0px) and (max-width: 425px){
    .servicesAndBrojects .logo-bg{
        display: none;
    }
}
