header{
    position: fixed;
    z-index: 99;
    width: 100%;
    height: 100px;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    width: 100%;
    transition: 0.4s ease;
}

nav .castle-logo{
    text-decoration: none;
    font-family: var(--main-font);
    text-transform: uppercase;
    font-size: 35px;
    color: white;
}
nav > .castle-logo span{
    color: var(--main-color);
}
nav .details-box{
    display: flex;
    gap: 8rem;
}
/*menu btn */
nav .details-box .menu-btn ,
nav .details-box .close-btn
{
    display: flex;
    align-items: flex-end;
    justify-content:space-between;
    flex-direction: column;
    width: 60px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
    height: 40px;
}
nav .details-box .menu-btn .hamburger-line{
    background-color: white;
    width: 40px;
    height: 2px;
    transition: 0.3s ease;

}
nav .details-box .menu-btn .hamburger-line:nth-of-type(2){
    width: 25px;
}
nav .details-box .menu-btn::after {
    content: 'MENU';
    color: white;
    position: absolute;
    right: 80px;
    letter-spacing: 2px;
    height: 20px;
    display: flex;
    align-items: center;
    font-size: 13px
}

/*close btn */
nav .details-box .close-btn{
    justify-content:center;
}
nav .details-box .close-btn .hamburger-line{
    background-color: white;
    width: 35px;
    height: 2px;
    position: absolute;
    transition: 0.3s ease-in-out;

}
nav .details-box .close-btn .hamburger-line:nth-of-type(2){
    display: none;
}
nav .details-box .close-btn .hamburger-line:first-of-type{
    transform: rotate(45deg);
}
nav .details-box .close-btn .hamburger-line:last-child{
    transform: rotate(-45deg);
}



  @media (min-width: 320px) and (max-width: 767px) {
  
    .box-btn.hide{
        display: none;
    }
    nav .castle-logo {
        font-size: 22px;
        font-weight: bold;
    }
    nav .details-box .menu-btn::after{
        display: none;
    }
    
  }


