.projects-box{
    padding-top: var(--top-bage-size);
    font-family: var(--site-font);
}


.projects-container{
    display: flex;
    flex-wrap: wrap;
    padding-left: 3rem;
    padding-right: 3rem;
    padding-top: 3rem;
    overflow: hidden;
    max-width: 1450px;
    margin: 0 auto;

}
.projects-box .project{
    display: flex;
    justify-content: center;
    position: relative;
    padding-bottom: 3rem;
    width: 50%;
}

.projects-box .project img{
    width: 90%;
    transition: 0.7s ease;
    filter: brightness(0.6);

}

.projects-box .project:hover >.project-bg{
    transform: perspective(1000px) rotateY(30deg);
    width: 90%;
    box-shadow: -18px 0px 0px #000000d9;
}
.projects-box .project .site-img{
    position: absolute;
    width: 55%;
    left: 65%;
    top: 5%;
    transform: perspective(1000px) rotateY(-60deg);
    opacity: 0;
    z-index: 1;
}
.projects-box .project:hover >.site-img{
    transform: none;
    opacity: 1;
}

.description-project-box{
    color: white;
    position: absolute;
    top: 65%;
    left: 8%;
    transition: 0.7s ease;


}.projects-box .project:hover > .description-project-box{
    top: 40%;
}
.description-project-box .description-project-tittle{
    font-size: 35px;
    font-family: var(--main-font);
    margin-bottom: 3px;
    
}
.description-project-box .description-project-subtittle{
    margin-bottom: 1rem;
}
.description-project-box .description-project-link{
    opacity: 0;
    font-weight: bold;
    text-decoration: none;
    color: white;
    transition: 1s ease;

}
.description-project-box .description-project-link::after{
    content: '';
    position: relative;
    border: solid var(--main-color);;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(-45deg);
    left: 10px;
}
.projects-box .project:hover .description-project-link{
    opacity: 1;
    color: var(--main-color);

}

.discuss-project{
    display: flex;
    color: white;
    flex-direction: column;
    align-items: center;
    padding-bottom: 3rem;

}
.discuss-project h3{
    font-size: 30px;
    margin-bottom: 2rem;

}


 /* tablets  */
@media (min-width: 768px) and (max-width: 1023.9px) {
    .projects-box .project img {
        width: 98%;
    }
    .description-project-box {
        top: 50%;
    }
    .description-project-box .description-project-tittle {
        font-size: 30px;
    }
    .description-project-box .description-project-subtittle {
        font-size: 15px;
    }
}

/* mobiles */
@media (min-width: 0px) and (max-width: 767.9px) {
    .projects-box .project {
        width: 100%;
    }
    .projects-container {
        padding-left: 0.5rem; 
        padding-right: 0.5rem;
    }
}

@media (min-width: 0px) and (max-width: 425px) {
    .description-project-box {
        top: 45%;
    }
    .projects-box .project .site-img {
        display: none;
    }
    .description-project-box .description-project-link {
        opacity: 1;
    }
    .description-project-box .description-project-tittle {
        font-size: 30px;
    }
    .description-project-box .description-project-subtittle {
        font-size: 15px;
    }
    .discuss-project h3 {
        font-size: 20px;
    }
}