.digital-waves-box{
    height: 100%;
    width: 100%;
    color: white;
    text-align: center;
    position: relative;
}
.waves-bg{
    width: 100%;
    opacity: 0.4;
    object-fit: cover;
    height: 650px;
}
.digital-waves-box .waves-details-box{
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%)
}

.waves-details-box h2{
    font-size: 30px;
    font-weight: bold;
}
.kinds-box{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3rem;

}
.kinds-box div{
    margin-right: 40px;
    white-space: nowrap;
    overflow: hidden;
}
.kinds-box div::after{
    content: '';
    width: 10%;
    height: 1.5px;
    background-color: var(--main-color);
    position: absolute;
    transform: translateX(-50%);
    bottom: -10%;
}
.kinds-box div p{
    font-size: 20px;
}
.kinds-box div h3{
    font-size: 30px;
}
.end-of-waves{
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translateX(-50%)
}
.end-of-waves h3{
    font-size: 30px;
    margin-bottom: 2rem;
}



@media (min-width: 0px) and (max-width: 786px) {
    .digital-waves-box .waves-details-box {
        top: 5%;
    }
    .digital-waves-box .sub-tittle {
        font-size: 17px;
    }
    .waves-details-box h2 {
        font-size: 25px;
    }
    .waves-bg {
    }
    .kinds-box {
        margin-top: 2rem;
        flex-direction: column;
    }
    .kinds-box div {
        margin-right: 0px;
        margin-bottom: 30px;
    }
    .kinds-box div::after {
        bottom: auto;
        width:18% ;
        margin-top: 2%;
    }
    .end-of-waves {
        top: 75%;
    }
    .end-of-waves h3 {
        font-size: 25px;
    }
}
