footer{
    background-color: black;
    font-family: var(--site-font);
    color: white;
    transition: 0.5s ease;
    padding: 3rem 3rem 1rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.ftr-ask-box{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 30px;
}
.ftr-ask-box h2{
    font-size: 60px;
    letter-spacing: 20px;
    cursor:context-menu;

}

footer:hover {
    background-color: white;
    color: black;
}
footer:hover .ftr-ask-box .box-btn{
    color: black;
    border-color: black;
}
footer:hover .ftr-menu ul li a{
    color: black;
}
.ftr-menu{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.ftr-menu ul {
    list-style: none;
    display: flex;
    gap: 30px;
}
.ftr-menu ul li a{
    color: white;
    text-decoration: none;
}