.customers-section{
    padding:  var(--top-bage-size) 0;
    background-image: linear-gradient(to top,#e4f0fa 42%,#f7fcfe 74%);
    text-align: center;
    position: relative;
    font-family: var(--site-font);
}

.number-bg{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    position: absolute;
    z-index: 0;
    color: var(--light-color);
    font-size: 300px;
    font-weight: bold;
    transform: translate(-50%);
    opacity: 0.2;
    top: 0;
}
.cust-desription-box{
    position: relative;
    z-index: 2;
    color: var(--secondary-color);
    font-size: 30px;
}

.cust-desription-box h2{
    font-size: 40px;
}
.cust-desription-box h4{
    margin: 30px 0;
}
.customers{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    flex-wrap: wrap;
    gap: 20px;

}
.customers .logo-container {
    width: calc((100%/5));
    height: 200px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
}
.customers .logo{
    width: 60%;
    object-fit: cover;
}


@media (min-width: 425px) and (max-width: 786px){
    .number-bg{
        font-size: 170px;
        }
        .customers .logo-container {
            max-width: 165px;
            height: 141px;
        }
        .customers .logo-container {
            width: calc((100%/3));
        }
}
@media (min-width: 0px) and (max-width: 425px){
    .number-bg{
        font-size: 100px;
        }
        .customers .logo-container {
            width: calc((100%/2));
            max-width: 165px;
            height: 141px;
        }

}